* {margin: 0; padding: 0;}
li {list-style: none;}
a {color: black; text-decoration: none;}
img {display: block;}

body {font-family: 'Malgun Gothic';}

/*-----------------join--------------------*/


#user_id, #user_pw, #user_pwchk, #user_name, #email, #email_add,
#user_add, #user_add_text, #tel_first, #tel_last, #birth_start, #recommender {
    width: 180px;
    height: 20px;
}

#id_message,
#pw_message {
    font-size: 14px;
    color: red;
    padding-left: 10px;
}


.form_wrap select {
    height: 24px;
    line-height: 24px;
}
.form_wrap .btn {
    cursor: pointer;
}

.form_wrap {
    width: 950px;
    margin: 0 auto;
}

.form_wrap .join_logo {
    width: 208px;
    height: 80px;
    margin: 50px auto;
}
.form_wrap .join_logo>img {
    width: 100%;
    height: 80px;
}
.form_wrap>h2 {
    height: 100px;
    line-height: 100px;
    text-align: center;
}
.form_wrap form h2 {
    height: 100px;
    line-height: 100px;
    text-align: center;
}
.form_wrap .info_title {
    height: 50px;
    line-height: 50px;
}
.form_wrap .info_title>div:first-child {
    width: 100px;
    font-size: 18px;
    font-weight: bold;
    float: left;
}
.form_wrap .info_title>div:last-child {
    width: 280px;
    float: right;
    text-align: right;
}


.box01 {
    width: 950px;
    height: auto;
    margin: 0 auto;

    border-top: 1px solid #ccc;
    border-collapse: collapse;
}
.box01 tr {
    height: 60px;
}
.box01 tr:first-child {
    height: 90px;
}
.box01 tr:nth-child(2) {
    height: 80px;
}
.box01 tr:nth-child(6) {
    height: 100px;
}
.box01 tr:nth-child(10) {
    height: 100px;
}
.box01 tr:nth-child(10) td div {
    height: 30px;
}


.box01 td {
    border-bottom: 1px solid #ccc;
}

.box01 td:first-child {
    background-color: #666;
    padding-left: 30px;
    color: white;
}
.box01 td:last-child {
    padding-left: 30px;
}
.box01 tr td div {
    padding-top: 5px;
    font-size: 14px;
}

.box01 .btn {
    width: 120px;
    height: 24px;
    line-height: 24px;
    text-align: center;
    border: 1px solid #ccc;
    border-radius: 3px;
}

#user_add_text {
    width: 310px;
    margin-top: 10px;
}
#tel_first, #tel_last {
    width: 100px;
}



.box02 {
    width: 950px;
    height: auto;
    margin: 0 auto;

    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
}
.box02 tr {
    height: 60px;
    line-height: 60px;
}
.box02 td:first-child {
    padding-left: 30px;
    width: 242.2px;
}
.box02 td:last-child {
    padding-left: 30px;
}


.bottom_btn {
    width: 950px;
    margin: 100px auto;
    text-align: center;
}
.bottom_btn>button {
    width: 100px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border: none;
    border-radius: 3px;
    color: white;
    cursor: pointer;
}
.bottom_btn>button:first-child {
    background-color: #666;
}
.bottom_btn>button:last-child {
    background-color: firebrick;
}
